import React, { useState } from 'react'

function HomePage(){
    const [state, setState] = useState("Doubting ...")
    const checkBack = async () => {
        try {
            const result = await fetch('check/')
            
            console.log("result:", result)
            setState("Yes")
        } catch (error) {
            console.log("err:",error)
            setState("Nope")
        }
    }
    return(
        <>
            <h1>Hello, welcome to my blog</h1>
            <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Est quisquam iste reiciendis magni, aspernatur voluptatibus dicta aperiam accusantium recusandae veritatis unde alias doloremque non perspiciatis cupiditate tenetur laboriosam enim. Quas!</p>
            <p>Lorem ipsum dolor sit, amet consectetur adipisicing elit. A doloremque at perferendis cumque odio! Odit, nisi, sequi qui recusandae libero adipisci placeat eaque ab, distinctio illo cum iure dolores asperiores?</p>
            <button id="backendStatusBtn" onClick={checkBack}>Is backend working?</button>
            <label htmlFor="backendStatusBtn">{state}</label>
        </>
    )
}

export default HomePage