import React from "react"
import { Link } from 'react-router-dom'

function NavBar(){
    return (

        <nav>
            <ul>
                <li>
                    <Link to = "/">Home</Link>
                </li>
                <li>
                    <Link to = "/about">About</Link>
                </li>
                <li>
                    <Link to = "/articles">Articles</Link>
                </li>
            </ul>
            <hr />
        </nav>
    )
}

export default NavBar